$max-width-canvas: 600px;
$line-height-new-entry: 44px;
$line-height-new-entry-detail: 30px;

@import 'general';
/* receipe */
/* receipe */
/* receipe */
body {
    background-image: radial-gradient( circle at bottom left , #663399 0%,#9966CC 50%,#CC99FF 100%);
}
.gr-txt {
    color: green;
}
#container-anchor {
    padding: 1em 0;
    height: 100%;
}
.inner-canvas {
    background: white;
    /*border-radius:2px;*/
    width: $max-width-canvas;
    font-family: "Open Sans";
    padding: 0.5em;
    position: relative;
    height: 100%;
    overflow-y: auto;
    p {
        display: block;
        margin-top: 1em;
        margin-bottom: 1em;
    }
    h1 {
        display: block;
        font-size: 2em;
        margin-top: 0.67em;
        margin-bottom: 0.67em;
        font-weight: bold;
    }
    dir,
    menu,
    ul {
        display: block;
        list-style-type: disc;
        margin-top: 1em;
        margin-bottom: 1em;
        padding: 40px;
    }
    li {
        display: list-item;
    }
}
