/* general code style-ing */

@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,800,600,700,300|Raleway:400,900,800,700,600,500,300,200|Lato:400,300,100);
@import url(https://fonts.googleapis.com/css?family=Ubuntu:700,500,400,300,300italic,400italic,500,500italic);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);

button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

html, body, div, dl, dt, dd, ul, ol, p, li, a, form, fieldset, input[type="text"], input[type="search"], blockquote, th, td {
  margin: 0;
  padding: 0;
}

ul ,ol {
  list-style: none;
}

a {
  outline: none !important;
  text-decoration: none;
  color:#000;
}

input:focus, textarea:focus, keygen:focus, select:focus {
  outline-offset: -2px;
}

div:before, select, * {
  box-sizing: border-box;
}

button, input {
  line-height: normal;
  outline:none;
  border:none;
}

html,
body {
  box-sizing: border-box;
  height: 100%;
  width:100%;
  margin: 0;
}

.flex-column-flow {
  display: flex;
  flex-direction: column;
}

.flex-row-flow {
  display: flex;
  flex-direction: row;
}

.flex-column-halign-center, .flex-row-valign-center {
  align-items: center;
}

.flex-column-vspace-around {
  justify-content: space-around;

}

.flex-column-valign-center, .flex-row-halign-center {
  justify-content: center;
  height: 100%;
}

.flex-stretch {
  align-items: stretch;
}

.h-center {
  text-align: center;
}

.hide {
  display: none;
  visibility: hidden;
}

.invisible {
  visibility: hidden;
}

.user-select-none {
  user-select: none;
  cursor: default;
}

.fit {
   width: fit-content;
}

.all-margin-auto {
  margin:auto;

}

